<!--博客管理组件-->
<template>
  <div class="">
    <!--筛选表单-->
    <el-form ref="form" :model="form" inline  size="small">
        <el-form-item>
          <el-input v-model="form.title"  placeholder="博客标题"></el-input>
        </el-form-item>
        <el-form-item>
          <el-select v-model="form.typeId" placeholder="博客分类">
            <el-option :label="type.name" :value="type.id" v-for="(type,index) in typeList" :key="index"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-select v-model="form.published" placeholder="状态">
            <el-option label="草稿" value="false"></el-option>
            <el-option label="发布" value="true"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-select v-model="form.recommend" placeholder="是否推荐">
            <el-option label="推荐文章" value="true"></el-option>
            <el-option label="普通文章" value="false"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" icon="el-icon-search" @click="onSubmit">搜索</el-button>
          <el-button type="primary" icon="el-icon-refresh" @click="reset">重置</el-button>
        </el-form-item>
      </el-form>
    <!--功能按钮-->
    <el-button type="primary" size="small" style="margin-right: 10px" @click="selectAll">全选</el-button>
    <el-popconfirm title="确定删除吗？" @confirm="batchDelete">
      <el-button slot="reference" type="danger" icon="el-icon-delete" size="small" style="margin-bottom: 15px" >批量删除</el-button>
    </el-popconfirm>
    <el-button type="primary" size="small" style="margin-left: 10px" @click="cancel">取消选择</el-button>
    <el-button type="primary" icon="el-icon-s-promotion" size="small" style="margin-bottom: 15px;margin-left: 10px" @click="releaseBlog">发布文章</el-button>
    <!--数据内容-->
    <el-table
          :data="tableData"
          stripe
          border
          size="small"
          style="width: 100%">

        <el-table-column
          align="center"
          width="50">
        <template slot-scope="scope">
          <el-checkbox v-model="scope.row.checked"  @change="select(scope.row)"></el-checkbox>
        </template>
      </el-table-column>

        <el-table-column
            type="index"
            label="序号"
            align="center"
            width="50">
        </el-table-column>
        <el-table-column
            label="标题"
            align="center"
            width="250">
          <template slot-scope="scope">
            <el-tag effect="dark" type="" v-if="scope.row.id%4===0">
              {{ scope.row.title }}
            </el-tag>
            <el-tag effect="dark" type="success" v-if="scope.row.id%4===1">
              {{ scope.row.title }}
            </el-tag>
            <el-tag effect="dark" type="danger" v-if="scope.row.id%4===2">
              {{ scope.row.title }}
            </el-tag>
            <el-tag effect="dark" type="warning" v-if="scope.row.id%4===3">
              {{ scope.row.title }}
            </el-tag>
          </template>
        </el-table-column>
        <!--自定义标签时  prop="title"就会失效  提供scope.row获取行对象   -->
        <el-table-column
            label="标记"
            align="center"
            width="100">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.flag" effect="dark">原创</el-tag>
            <el-tag v-else="scope.row.flag" type="warning" effect="dark">转载</el-tag>
          </template>
        </el-table-column>
        <el-table-column
            prop="imageUrl"
            label="首图"
            align="center"
            width="120">
          <template slot-scope="scope">
            <el-image
                style="width: 100px; height: 50px"
                :src="scope.row.imageUrl"
                lazy
                :preview-src-list="[scope.row.imageUrl]">
            </el-image>
          </template>
        </el-table-column>
        <el-table-column
            prop="typeId"
            align="center"
            label="分类"
            width="100">
          <template slot-scope="scope">
            <el-tag  effect="dark">{{scope.row.typeName}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column
            label="文章状态"
            align="center"
            width="100">
          <template slot-scope="scope">
            <el-switch
                class="tableScopeSwitch"
                :active-value="true"
                :inactive-value="false"
                @change="handleStatusChange(scope.$index, scope.row)"
                active-text="发布"
                inactive-text="草稿"
                v-model="scope.row.published">
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column
            prop="views"
            label="浏览数"
            align="center"
            sortable
            width="100">
        </el-table-column>
        <el-table-column
            prop="commentNumber"
            sortable
            label="评论数"
            align="center"
            width="100">
        </el-table-column>
        <el-table-column
            label="是否开启赞赏"
            align="center"
            width="100">
          <template slot-scope="scope">
            <el-switch
                class="tableScopeSwitch"
                :active-value="true"
                :inactive-value="false"
                @change="handleStatusChange(scope.$index, scope.row)"
                active-text="启用"
                inactive-text="禁用"
                v-model="scope.row.appreciation">
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column
            label="是否为推荐文章"
            align="center"
            width="100">
          <template slot-scope="scope">
            <el-switch
                class="tableScopeSwitch"
                :active-value="true"
                :inactive-value="false"
                @change="handleStatusChange(scope.$index, scope.row)"
                active-text="推荐"
                inactive-text="普通"
                v-model="scope.row.recommend">
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column
            label="是否开启文章声明"
            align="center"
            width="100">
          <template slot-scope="scope">
            <el-switch
                class="tableScopeSwitch"
                :active-value="true"
                :inactive-value="false"
                @change="handleStatusChange(scope.$index, scope.row)"
                active-text="开启"
                inactive-text="关闭"
                v-model="scope.row.shareStatement">
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column
          label="是开启评论"
          align="center"
          width="100">
        <template slot-scope="scope">
          <el-switch
              class="tableScopeSwitch"
              :active-value="true"
              :inactive-value="false"
              @change="handleStatusChange(scope.$index, scope.row)"
              active-text="开启"
              inactive-text="关闭"
              v-model="scope.row.commentabled">
          </el-switch>
        </template>
      </el-table-column>
        <el-table-column
            fixed="right"
            label="操作"
            align="center"
            width="250">
          <template slot-scope="scope">
            <el-row>
<!--              <el-col :span="8">-->
<!--                <el-button type="info" icon="el-icon-view" @click="handleClick(scope.row)" size="small">查看</el-button>-->
<!--              </el-col>-->
              <el-col :span="12">
                <el-button type="primary" icon="el-icon-edit" size="small" @click="editBlog(scope.row)">编辑</el-button>
              </el-col>
              <el-col :span="12">
                <el-popconfirm title="确定删除吗？"  @confirm="deleteBlog(scope.row)">
                  <el-button slot="reference" type="danger" icon="el-icon-delete"   size="small">删除</el-button>
                </el-popconfirm>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
      </el-table>
    <!--分页-->
    <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          background
          :current-page.sync="currentPage"
          :page-sizes="[6, 8, 10, 12]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
      </el-pagination>
  </div>
</template>

<script>

import { selectAllType } from '@/api/type.js'
import request from "../../utils/request";
import router from "../../router";
export default {
  name: "BlogIndex",
  components: {},
  data() {
    return {
      currentPage: 1,//当前页码
      pageSize:6,//当前每页显示条数
      total: 0,//总记录数
      //筛选表单数据
      form: {
        title: '',//博客标题
        typeId: '',//博客分类
        published:'',//博客状态（发布草稿）
        recommend:'',//博客是否推荐
      },
      typeList:[],//所有分类列表
      //表格数据
      tableData: [
        {
          appreciation:true,//是否开启赞赏
          commentNumber:'200',//评论数
          commentabled:true,//是否开启评论
          content:'',//文章内容
          createTime:'',//文章创建时间
          description:'',//文章摘要
          flag: '',//原创或者转载
          id:'',//文章id
          imageId:'',//文章首图id
          imageUrl:"",//首图URL
          published:true,//文章是否发布
          recommend:false,//是否为推荐文章
          shareStatement:true,//是否开启声明
          statementMessage:'',//文章声明
          title: '博客标题',
          typeId: '',//文章分类id
          typeName:'',//文章分类名称
          updateTime:'',//文章更新时间
          views: 200333,//文章浏览量
          checked:'',//选中状态
        },
      ],
      //批量删除id
      ids:[],
    }
  },
//页面加载
  created() {
    //获取分类
    this.loadType()
    //获取文章列表
    this.loadBlogPage()
  },
//可以访问DOM元素
  mounted() {
    //刷新数据
    this.loadBlogPage()
  },
//方法集合
  methods: {
    //复选框选择方法
    select(row){
      if(row.checked){
        //被选中
        this.ids.push(row.id)
      }else {
        //取消选中
        this.removeArray(this.ids,row.id)
      }
      console.log(row)
    },
    //删除指定元素
    removeArray(arr, val) {
      for(let i = 0; i < arr.length; i++) {
        if(arr[i] === val) {
          arr.splice(i, 1);
          break;
        }
      }
    },
    //全选按钮
    selectAll(){
      if(this.ids.length==this.pageSize){
        this.$message.error("已经全选，不要重复点击")
      }
      for (let i=0;i<this.tableData.length;i++){
        this.tableData[i].checked=true;
        this.ids.push(this.tableData[i].id)
      }
    },
    //取消按钮
    cancel(){
      for (let i=0;i<this.tableData.length;i++){
        this.tableData[i].checked=false;
      }
      this.ids=[]
    },

    //批量删除方法
    batchDelete(){
      if(this.ids.length===0){
        this.$message.error("请选选择在进行批量删除")
      }else {
        for ( let i=0;i<this.ids.length;i++){
          request.delete("/blog/deleteBlog/"+this.ids[i]).then(res=>{
            if(res.data.result){
              this.$message.success("删除成功")
            }else {
              this.$message.error("删除失败")
            }
          })
        }
        //刷新数据
        this.loadBlogPage()
        this.ids=[]
      }
    },
    //获取文章列表方法
    loadBlogPage(){
      request.post(
          "/blog/queryBlogAllPage",
          {blogVo:this.form,pageNum:this.currentPage,pageSize:this.pageSize}).then(res=>{
        //设置总记录数
        this.total=res.data.data.blogPage.total
        //设置表格数据
        this.tableData=res.data.data.blogPage.results
        //设置当前页显示条数
        this.pageSize=res.data.data.blogPage.pageSize
        //设置当前页码
        this.currentPage=res.data.data.blogPage.currentPage
      })
    },
    //重置按钮
    reset(){
      this.form={}
    },
    //筛选表单提交功能
    onSubmit() {
      this.loadBlogPage();
    },
    //任意开关改变触发
    handleStatusChange(index,row){
      request.patch("/blog/patchBlog",row).then(res=>{
        if(res.data.result){
          this.$message.success("更该成功")
          this.loadBlogPage()
        }else {
          this.$message.error("更该失败")
        }
      })
    },
    //改变当前每一页个数
    handleSizeChange(pageSize) {
      this.pageSize=pageSize
      this.loadBlogPage()
    },
    //改变当前页码触发
    handleCurrentChange(pageNum) {
      this.currentPage=pageNum
      this.loadBlogPage()
    },
    //获取所有分类
    loadType(){
      selectAllType().then(res=>{
        this.typeList=res.data.data.types
      })
    },
    //发布按钮方法
    releaseBlog(){
      router.push({path:'/layout/releaseBlogIndex'})
    },
    //编辑按钮
    editBlog(row){
      //根据id查询标签id集合
      request.get("/tag/getTagIdsByBlogId/"+row.id).then(res=>{
        //将标签id集合存入浏览器
        sessionStorage.setItem("blog",JSON.stringify(row))
        //将文章数据存入浏览器
        sessionStorage.setItem("tagIds",JSON.stringify(res.data.data.tagByBlogId))
        router.push({path:'/layout/releaseBlogIndex'})
      })
    },
    //删除按钮
    deleteBlog(row){
      request.delete("/blog/deleteBlog/"+row.id).then(res=>{
        if(res.data.result){
          this.$message.success("删除成功")
          this.loadBlogPage()
        }else {
          this.$message.error("删除失败")
        }
      })
    },


    //查看按钮方法
    // handleClick(row) {
    //   console.log(row);
    // },

  },
}
</script>

<style >
.tableScopeSwitch .el-switch__label {
  position: absolute;
  display: none;
  /*字体颜色*/
  color: #fff;
}
/*打开时文字位置设置*/
.tableScopeSwitch .el-switch__label--right {
  z-index: 1;
  right: 6px;    /*不同场景下可能不同，自行调整*/
}
/*关闭时文字位置设置*/
.tableScopeSwitch .el-switch__label--left {
  z-index: 1;
  left: 6px;    /*不同场景下可能不同，自行调整*/
}
/*显示文字*/
.tableScopeSwitch .el-switch__label.is-active {
  display: block;
}
.tableScopeSwitch.el-switch .el-switch__core,
.el-switch .el-switch__label {
  width: 75px !important;    /*开关按钮的宽度大小*/
}
.el-switch__core{
  /*开关为激活颜色*/
  background:#f07810;
 }
</style>



